import React from 'react'

import * as P from "./ComponentPart"

import { fetch_data } 	from "./service_api"

export class LoadContent extends React.PureComponent {
	state = { isLoading: false, data: null }

	update = (path, state) => {
		const self = this

		self.setState({isLoading: true})
		fetch_data(path, {state}).then(json=>{
			self.setState({isLoading: false, data:json})
		})
	}

	componentDidMount() {
		this.update(this.props.path, this.props.state)
	}

	componentWillReceiveProps(nextProps) {
		this.update(nextProps.path, nextProps.state)
	}

	render() {
		if (this.state.isLoading) {
			return <P.CenterInfo label="Loading content..." />
		}

		if (!this.state.data) {
			return <P.CenterInfo label="No data received..." />
		}

		return <React.Fragment> { this.props.children(this.state.data) } </React.Fragment>
	}	
}



