import React from 'react'

import * as v from "./vendor"
import * as P from "./ComponentPart"

import { uiStore }   from "./service_store_ui"
import { userStore } from "./service_store_user"

import { PageSites }  			  from "./PageSites"
import { PageFinance }  		  from "./PageFinance"
import { PageFinanceScreener }    from "./PageFinanceScreener"
import { PageFinanceStock } 	  from "./PageFinanceStock"
import { PageFinanceStockSearch } from "./PageFinanceStockSearch"

import { PageTest } from "./PageTest"

const routes = {
	"/sites":  					PageSites,
	"/finance":  				PageFinance,
	"/finance/screener":  		PageFinanceScreener,

	"/finance/stock": 			PageFinanceStock,
	"/finance/stock/search": 	PageFinanceStockSearch,

	"/test":  					PageTest,
}

const RouteToPage = ({path, state}) => {
	const Page = routes[path]

	if (Page) {
		return <Page path={path} state={state} />
	}

	return <P.CenterInfo label={`404: ${path} not found`} />
}

const RenderMain = v.inject("ui")(v.observer(({ui})=>(
	<P.ContentMobile display={true}>
		<RouteToPage path={ui.Main} state={ui.MainState} />
	</P.ContentMobile>
)))

const RenderMinor = v.inject("ui")(v.observer(({ui})=>(
	<P.ContentMobile display={ui.MinorIsOpen}>
		<RouteToPage path={ui.Minor} state={ui.MinorState} />
	</P.ContentMobile>
)))

const RenderSide = v.inject("ui")(v.observer(({ui})=>(
	<P.ContentMobile display={ui.SideIsOpen}>
		<RouteToPage path={ui.Side} state={ui.SideState} />
	</P.ContentMobile>
)))

const RenderCross = v.inject("ui")(v.observer(({ui})=>(
	<P.ContentMobile display={ui.CrossIsOpen}>
		<RouteToPage path={ui.Cross} state={ui.CrossState} />
	</P.ContentMobile>
)))

const Mobile = () => <React.Fragment><RenderMain /><RenderMinor /><RenderSide /><RenderCross /></React.Fragment>

const MessageBar = v.inject("ui")(v.observer(({ui})=>(
	<v.Snackbar
		open={ui.HasMessage}
		autoHideDuration={2500}
		onClose={ui.DelMessage}
		SnackbarContentProps={{'aria-describedby': 'messageBar'}}
		message={<span id="messageBar">{ui.CurrentMessage}</span>}
		action={[
			<v.IconButton
				key="close"
				aria-label="Close"
				color="secondary"
				onClick={ui.DelMessage}
			>
				<v.CloseIcon />
			</v.IconButton>
		]}
	/>
)))

const MainMenu = v.inject("ui")(v.observer(({ui})=>{
	if (ui.CrossIsOpen) {
		return <P.NavBarIcon Icon={v.CancelIcon} isLeft label="Close" onClick={ui.Close} />
	}

	if (ui.SideIsOpen) {
		return <P.NavBarIcon Icon={v.CloseIcon} isLeft label="Close" onClick={ui.Close} />
	}

	if (ui.MinorIsOpen) {
		return <P.NavBarIcon Icon={v.ArrowBackIcon} isLeft label="Close" onClick={ui.Close} />
	}

	return <P.NavBarIcon Icon={v.MenuIcon} isLeft label="Menu" onClick={ui.OpenLeftDrawer} />
}))

const MainTitle = v.inject("ui")(v.observer(({ui})=>{
	return <P.NavBarTitle label={ui.Title} />
}))

const Loading = v.inject("ui")(v.observer(({ui})=>{
	if (ui.IsLoading) {
		return <P.LoadingBar />
	}
	return null
}))

const LeftDrawer = v.inject("ui")(v.observer(({ui})=>{
	const Item = ({path, label, Icon}) => (
		<v.ListItem button onClick={()=>ui.OpenMain(path)}>
			<v.ListItemIcon><Icon/></v.ListItemIcon><v.ListItemText primary={label} />
		</v.ListItem>
	)

	return (
		<v.Drawer anchor="left" open={ui.LeftDrawerIsOpen} onClose={ui.CloseLeftDrawer}>
			<div
				tabIndex={0}
				role="button"
				onClick={ui.CloseLeftDrawer}
				onKeyDown={ui.CloseLeftDrawer}
			>
				<div style={{width:250}}>
					<P.NavBar>
						<P.NavBarTitle label="Apps" />
					</P.NavBar>
					<v.List component="nav">
						<Item path="/sites"    label="网址导航" Icon={v.LinkIcon}/>
						<Item path="/finance"  label="美股"     Icon={v.AttachMoneyIcon} />
						<Item path="/reading"  label="阅读"     Icon={v.ChromeReaderModeIcon} />
						<Item path="/favorite" label="收藏夹"   Icon={v.StarIcon} />
						<Item path="/yi" 	   label="起卦"     Icon={v.Filter8Icon} />
					</v.List>
					<v.Divider />
					<v.List component="nav">
						<Item path="/test" 	   label="Test"     Icon={v.AppsIcon} />
					</v.List>
					<v.Divider />
					<v.List component="nav">
						<v.ListItem button onClick={()=>{ui.SetAuth(false); localStorage.clear()}}>
							<v.ListItemIcon><v.ExitToAppIcon/></v.ListItemIcon><v.ListItemText primary="退出" />
						</v.ListItem>
					</v.List>
				</div>
			</div>
		</v.Drawer>
	)
}))

class PageAuth extends React.Component {
	state = {
		current:"login",
		registerEmail:"", p1:"", p2:"",
		loginEmail:"", pwd:"",
		forgetEmail:"",
	}

	handleChange  = name => evt => this.setState({ [name]: evt.target.value })
	changeCurrent = (current) => () => this.setState({current})

	doRegister = () => {
		this.state.p1 === this.state.p2 && this.props.user.Register(this.state.registerEmail, this.state.p1, this.state.p2, window.navigator.language)
	}
	doLogin    = () => {
		this.props.user.Login(this.state.loginEmail, this.state.pwd)
	}
	doForget   = () => console.log(this.state.forgetEmail)

	render() {
		const { current } = this.state
		const { ui } = this.props

		if (current === "login") {
			const { loginEmail, pwd } = this.state
			return (
				<P.CenterColumn>
					<div style={{marginTop:73}}></div>
					<P.Input placeholder="邮箱" value={loginEmail} onChange={this.handleChange("loginEmail")} />
					<P.Input placeholder="密码" value={pwd}        onChange={this.handleChange("pwd")} type="password" />
					<P.CenterRow>
						<v.Button color="primary" onClick={this.doLogin} variant="raised" disabled={ui.IsLoading}>登录</v.Button>
						<v.Button color="primary" onClick={this.changeCurrent("register")}>我要注册</v.Button>
						<v.Button color="primary" onClick={this.changeCurrent("forget")}>忘记密码</v.Button>
					</P.CenterRow>
				</P.CenterColumn>
			)
		}

		if (current === "register") {
			const { registerEmail, p1, p2 } = this.state
			return (
				<P.CenterColumn>
					<P.Input placeholder="邮箱" 	    value={registerEmail} onChange={this.handleChange("registerEmail")} />
					<P.Input placeholder="密码" 	    value={p1} 			  onChange={this.handleChange("p1")} type="password" />
					<P.Input placeholder="再次输入密码" value={p2} 			  onChange={this.handleChange("p2")} type="password" />
					<P.CenterRow>
						<v.Button color="primary" onClick={this.changeCurrent("login")}>我要登录</v.Button>
						<v.Button color="primary" onClick={this.doRegister} variant="raised" disabled={ui.IsLoading}>注册</v.Button>
						<v.Button color="primary" onClick={this.changeCurrent("forget")}>忘记密码</v.Button>
					</P.CenterRow>
				</P.CenterColumn>
			)
		}

		const { forgetEmail } = this.state

		return (
			<P.CenterColumn>
				<div style={{marginTop:146}}></div>
				<P.Input placeholder="发送随机新密码到注册时用的邮箱" value={forgetEmail} onChange={this.handleChange("forgetEmail")} />
				<P.CenterRow>
					<v.Button color="primary" onClick={this.changeCurrent("login")}>我要登录</v.Button>
					<v.Button color="primary" onClick={this.changeCurrent("register")}>我要注册</v.Button>
					<v.Button color="primary" onClick={this.doForget} variant="raised">发送</v.Button>
				</P.CenterRow>
			</P.CenterColumn>
		)		
	}
}
PageAuth = v.inject("ui", "user")(v.observer(PageAuth))

const AppMain = v.inject("ui")(v.observer(({ui})=>{
	if (!ui.IsLogin) {
		return (
			<React.Fragment>
				<Loading />
				<P.NavBar><P.NavBarTitle label="未登录" /></P.NavBar>
				<PageAuth />
				<MessageBar />
			</React.Fragment>
		)
	}

	const queryStock = symbol => ui.OpenSide("/finance/stock", symbol)

	return (
		<React.Fragment>
			<Loading />
			<P.NavBar>
				<MainMenu />
				<MainTitle />
				{ window._.isMobile ? 
					<P.NavBarIcon Icon={v.SearchIcon} label="search" onClick={()=>ui.OpenCross("/finance/stock/search")} />
					:
					<P.SearchBox queryStock={queryStock} />
				}
				<P.NavBarIcon Icon={v.AddIcon} label="Notifications" onClick={() => ui.OpenCross("/notes")} />
			</P.NavBar>
			<Mobile />
			<LeftDrawer />
			<MessageBar />
		</React.Fragment>
	)
}))

export const App = () => <v.Provider ui={uiStore} user={userStore}><AppMain /></v.Provider>



