import React from 'react'

import * as v from "./vendor"

const make = (text, href) => ({text, href})

const One = ({text, href}) => <v.Button target="_blank" href={href} color="primary">{text}</v.Button>

const Group = ({label, sites}) => (
	<React.Fragment>
		<v.CardHeader
			title={label}
		/>
		<v.CardContent>
			{
				sites.map(site=><One key={site.text} text={site.text} href={site.href} />)
			}
		</v.CardContent>
	</React.Fragment>
)

