import React from 'react'

import * as v from "./vendor"
import * as P from "./ComponentPart"

export const PageFinanceStockSearch = v.inject("ui")(v.observer(({ui})=>{
	const queryStock = e => {
		if (e.which === 13) {
			ui.CloseCross()
			ui.OpenSide("/finance/stock", e.target.value.toUpperCase())
		}
	}
	return (
		<div>
			<P.Input placeholder="股票代码" onKeyDown={queryStock} />
		</div>
	)
}))



