import * as v from "./vendor"
import * as u from "./utils"

const UIModel = v.types.model("UIModel", {
	// main routes
	Main: 	   "/sites",
	MainState: "",

	Minor: 		"",
	MinorState: "",

	// side routes
	Side: 	   "",
	SideState: "",

	Cross: 	   "",
	CrossState: "",

	IsLogin: false,

	LeftDrawerIsOpen: false,

	IsLoading: false,
	LoadingCounter: 0,

	Messages: v.types.optional(v.types.array(v.types.string), []),
})
.views(self => ({
	get MinorIsOpen() { return self.Minor !== "" },
	get SideIsOpen() { return self.Side !== "" },
	get CrossIsOpen() { return self.Cross !== "" },

	get Title() {
		const path = function(){
			if (self.CrossIsOpen) {
				return self.Cross
			} else if (self.SideIsOpen) {
				return self.Side
			} else if (self.MinorIsOpen) {
				return self.Minor
			}
			return self.Main
		}()
		const state = function(){
			if (self.CrossIsOpen) {
				return self.CrossState
			} else if (self.SideIsOpen) {
				return self.SideState
			} else if (self.MinorIsOpen) {
				return self.MinorState
			}
			return self.MainState
		}()
		switch (path) {
			case "/finance":
				return "美股"
			case "/finance/screener":
				return u.ScreenerMap[state] || u.PerformanceMap[state] || "行业股票单"
			case "/finance/stock":
				return self.SideState
			case "/finance/stock/search":
				return "查找股票"
			case "/sites":
				return "导航"
			default:
				return path
		}
	},

	get HasMessage() { return self.Messages.length > 0 },
	get CurrentMessage() { return self.Messages.peek()[0] || "" },
}))
.actions(self => {
	function _toggleMain(c, s) {
		self.Main = c
		self.MainState = s || ""
	}
	function _toggleMinor(c, s) {
		self.Minor = c
		self.MinorState = s || ""
	}

	function _toggleSide(c, s) {
		self.Side = c
		self.SideState = s || ""
	}

	function _toggleCross(c, s) {
		self.Cross = c
		self.CrossState = s || ""
	}

	return {
		afterCreate: () => {
			v.applySnapshot(self, u.Storage.UI())
			self.LoadingCounter = 0 // 防止在Loading的时候刷新页面，导致LoadingCounter出现问题
		},

		SetAuth: c => self.IsLogin = c,

		OpenMain:   (c, s) => _toggleMain(c, s),
		OpenMinor:  (c, s) => _toggleMinor(c, s),
		OpenSide:   (c, s) => _toggleSide(c, s),
		OpenCross:  (c, s) => _toggleCross(c, s),
		CloseCross: (c, s) => _toggleCross(""),

		Close: () => {
			if (self.CrossIsOpen) {
				_toggleCross("")
				return
			} else if (self.SideIsOpen) {
				_toggleSide("")
				return
			} else if (self.MinorIsOpen) {
				_toggleMinor("")
				return
			} else if (self.LeftDrawerIsOpen) {
				self.LeftDrawerIsOpen = false
			}
		},

		LoadingOn:  () => {self.IsLoading = true; self.LoadingCounter++},
		LoadingOff: () => {
			self.LoadingCounter--
			if (self.LoadingCounter === 0) {
				self.IsLoading = false
			}
		},

		OpenLeftDrawer:   () => self.LeftDrawerIsOpen = true,
		CloseLeftDrawer:  () => self.LeftDrawerIsOpen = false,
		ToggleLeftDrawer: () => self.LeftDrawerIsOpen = !self.LeftDrawerIsOpen,

		PutMessage:  s => s !== "" && self.Messages.push(s), //往后面添加
		DelMessage: () => self.Messages.shift(), //弹出最前面的
	}
})

export const uiStore = UIModel.create()

window.addEventListener("popstate", function(){
	uiStore.Close()
})

window.addEventListener("keydown", function(e){
	if (e.code === "Backquote") {
		uiStore.ToggleLeftDrawer()
	}

	if (e.code === "Escape") {
		uiStore.Close()
	}
})

v.onSnapshot(uiStore, snapshot=>{
	if (snapshot.IsLogin) {
		u.Storage.PutUI(snapshot)
	}
})



