import React from 'react'

import * as v from "./vendor"

export const NavBar = v.withStyles(
	theme => ({
		toolbar: {
			minHeight:theme.spacing.unit * 7,
		},
		appbar: {
			boxShadow:"none",
		}
	})
)(
	({ classes, children, color }) => 
	<v.AppBar className={classes.appbar} position="static" color={color || "primary"}>
		<v.Toolbar className={classes.toolbar}>
			{children}
		</v.Toolbar>
	</v.AppBar>
)

export const NavBarTitle = v.withStyles(
	theme => ({
		title: {
			marginLeft: theme.spacing.unit,
			flex: 1,
		}
	})
)(
	({ classes, label }) => 
	<v.Typography variant="title" color="inherit" className={classes.title}>
		{label}
	</v.Typography>
)

export const NavBarIcon = v.withStyles(
	theme => ({
		leftButton: {
			marginLeft: theme.spacing.unit * -1,
		},
		rightButton: {
			marginRight: theme.spacing.unit * -1,
		}
	})
)(
	({ classes, isLeft, Icon, label, onClick }) => {
		const className = function(){
			if (isLeft) {
				return classes.leftButton
			}
			return classes.rightButton
		}()
		return (
			<v.IconButton color="inherit" aria-label={label} className={className} onClick={onClick}>
				<Icon />
			</v.IconButton>
		)
	}

)

export const ContentMobile = v.withStyles(
	theme => ({
		wrapper: {
			backgroundColor:"white",
			height: window._.fullHeight - window._.toolbarHeight,
			position: "absolute",
			top: window._.toolbarHeight,
			right:0,
			bottom:0,
			left:0,
		},
		inner: {
			maxWidth: window._.fullWidth,
			margin:"auto",
		}
	})
)(
	({ classes, children, display }) => {
		return (
			<div className={classes.wrapper} style={{zIndex:display || -1000}}>
				<v.Scrollbars>
					<div className={classes.inner}>{children}</div>
				</v.Scrollbars>
			</div>
		)
	}
)

