export const LoadingBar = v.withStyles(
	{
		root: {
			position: "absolute",
			top: 0,
			left: 0,
			right: 0,
		}
	}
)(
	({ classes }) => 
	<div className={classes.root}>
		<v.LinearProgress color="secondary" />
	</div>
)

export const CenterInfo = v.withStyles(
	{
		root: {
			display:"flex",
			height:window._.fullHeight*0.618,
			justifyContent:"center",
			alignItems:"center",
			flexDirection:"column",	
		}
	}
)(
	({ classes, label }) => 
	<div className={classes.root}>
		<p>{label}</p>
	</div>
)

export const CenterColumn = v.withStyles(
	{
		root: {
			display:"flex",
			height:window._.fullHeight*0.618,
			justifyContent:"center",
			alignItems:"center",
			flexDirection:"column",	
		}
	}
)(
	({ classes, children }) => 
	<div className={classes.root}>
		{children}
	</div>
)

export const CenterRow = v.withStyles(
	{
		root: {
			display:"flex",
			justifyContent:"center",
			alignItems:"center",
			flexDirection:"row",
			marginTop: 16,
		}
	}
)(
	({ classes, children }) => 
	<div className={classes.root}>
		{children}
	</div>
)

// material-ui/docs/src/modules/components/AppSearch.js
export const SearchBox = v.withStyles(
	theme => ({
		wrapper: {
			fontFamily: theme.typography.fontFamily,
			position: 'relative',
			marginRight: theme.spacing.unit * 2,
			marginLeft: theme.spacing.unit,
			borderRadius: 2,
			background: v.fade(theme.palette.common.white, 0.15),
			'&:hover': {
				background: v.fade(theme.palette.common.white, 0.25),
			},
			'& $input': {
				transition: theme.transitions.create('width'),
				width: 200,
				'&:focus': {width: 250},
			},
		},
		search: {
			width: theme.spacing.unit * 9,
			height: '100%',
			position: 'absolute',
			pointerEvents: 'none',
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'center',
		},
		input: {
			font: 'inherit',
			padding: `${theme.spacing.unit}px ${theme.spacing.unit}px ${theme.spacing.unit}px ${theme.spacing.unit * 9}px`,
			border: 0,
			display: 'block',
			verticalAlign: 'middle',
			whiteSpace: 'normal',
			background: 'none',
			margin: 0, // Reset for Safari
			color: 'inherit',
			width: '100%',
			'&:focus': {outline: 0},
		}
	})
)(
	({ classes, queryStock }) => {
		const onKeyDown = e => {
			if (e.which === 13) {
				queryStock(e.target.value.toUpperCase())
				e.target.value = ""
			}
		}

		return (
			<div className={classes.wrapper}>
				<div className={classes.search}>
					<v.SearchIcon />
				</div>
				<input id="search" className={classes.input} onKeyDown={onKeyDown} />
			</div>
		)
	}
)

export const Input = v.withStyles(
	theme => ({
		root: {
			margin:theme.spacing.unit * 2,
			width:window._.isMobile ? 'calc(100% - 24px)' : 350,
		},
		textFieldRoot: {
			padding: 0,
			'label + &':{
				marginTop: theme.spacing.unit * 3,
			},
		},
		textFieldInput: {
			borderRadius: 4,
			backgroundColor: theme.palette.common.white,
			border: '1px solid #ced4da',
			fontSize: 16,
			padding: '10px 12px',
			width: 'calc(100% - 24px)',
			transition: theme.transitions.create(['border-color', 'box-shadow']),
			'&:focus': {
				borderColor: '#80bdff',
				boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
			},
		},
		textFieldFormLabel: {
			fontSize: 18,
		}
	})
)(
	({ classes, label, placeholder, onChange, onKeyDown, value, type }) => 
	<v.TextField
		placeholder={placeholder}
		label={label}
		value={value}
		type={type || "text"}
		InputProps={{
			disableUnderline: true,
			classes:{
				root: classes.textFieldRoot,
				input: classes.textFieldInput,
			},
		}}
		InputLabelProps={{
			shrink: true,
			className: classes.textFieldFormLabel,
		}}
		className={classes.root}
		onChange={onChange}
		onKeyDown={onKeyDown}
	/>
)

export const CheckRender = ({label}) => {
	console.log("Renderring: ",label)
	return null
}



