import * as v from "./vendor"

// https://github.com/mobxjs/mobx-state-tree/tree/master/packages/mst-middlewares#atomic
// https://github.com/mobxjs/mobx-state-tree/blob/master/packages/mst-middlewares/src/atomic.ts
export const mst_mw_atomic = v.createActionTrackingMiddleware({
	filter: call => call.parentId === 0,
	onStart: call => v.recordPatches(call.tree),
	onResume: (call, recorder) => recorder.resume(),
	onSuspend: (call, recorder) => recorder.stop(),
	onSuccess: (call, recorder) => {},
	onFail: (call, recorder) => recorder.undo()
})

// local storage

const storage = {
	Put: (k,v) => localStorage.setItem(k, JSON.stringify(v)),
	Get: (k) => (JSON.parse(localStorage.getItem(k)) || {}),
}

export const Storage = {
	PutUI:   (v)=>storage.Put("UIStore", v),
	PutUser: (v)=>storage.Put("UserStore", v),

	UI:   ()=>storage.Get("UIStore"),
	User: ()=>storage.Get("UserStore"),
}

