// init

function init() {
	// const
	window._ = {
		toolbarHeight: 	56,
		fullHeight: 	window.innerHeight,
		fullWidth: 		Math.min(window.innerWidth, 768),

		isMobile: function(){
			for (let key of ["Android", "iPhone", "iPad"]) {
				if (navigator.userAgent.indexOf(key) >= 0) {
					return true
				}
			}
			return false
		}(),
	}

	// stop backspace
	const initState = "initApp-htyapp"
	window.history.replaceState(initState, null, "/")
	window.history.pushState(null, null, "/")
	window.onpopstate = function historyChange(e){
		if (e.state === initState) {
			window.history.pushState(null, null, "/")
		}
	}
}

init()



