import React from 'react'

import * as v from "./vendor"
import * as P from "./ComponentPart"

const make = (Sym, Price, Change, Company, Industry, Perf1M, Perf3M, Perf1Y)=>({Sym, Price, Change, Company, Industry, Perf1M, Perf3M, Perf1Y})

const data = [
	make("GOOGL", 100, 0.03, "Google.com", "intersdasdasdnet", 0.10, 0.30, 0.50),
	make("NFLX", 100, 0.03, "Gooasdasdasdasdgle.com", "internet", 0.10, 0.30, 0.50),
	make("F", 100, 0.03, "Google.com", "internet", 0.10, 0.30, 0.50),
	make("AAPL", 100, 0.03, "Google.com", "internet", 0.10, 0.30, 0.50),
	make("AMZN", 100, 0.03, "Googasdasdasdasle.com", "internet", 0.10, 0.30, 0.50),
	make("AA", 100, 0.03, "Google.com", "inteasdasdasdasdasdrnet", 0.10, 0.30, 0.50),
	make("SWKS", 100, 0.03, "Google.com", "internet", 0.10, 0.30, 0.50),
]

const Cell = ({children}) => <v.TableCell style={{padding:4}}>{children}</v.TableCell>

export const PageTest = v.inject("ui")(v.observer(({ui, state})=>(
	<div>
		<P.NavBar color="default">
			<P.NavBarTitle label="1/13" />
		</P.NavBar>
		<v.CardContent>
			<div style={{width:60, float:"left"}}>
				<v.Table>
					<v.TableHead><v.TableRow><Cell>Symbol</Cell></v.TableRow></v.TableHead>
					<v.TableBody>
				{
					data.map(item=>(
						<v.TableRow key={item.Sym}><Cell>{item.Sym}</Cell></v.TableRow>
					))
				}
					</v.TableBody>
				</v.Table>
			</div>
			<div style={{width:"calc(100% - 60px)", float:"left"}}>
			<v.Scrollbars autoHeight autoHeightMin={window._.fullHeight - window._.toolbarHeight*2-32} autoHeightMax={window._.fullHeight - window._.toolbarHeight*2-32}>
				<v.Table>
					<v.TableHead>
						<v.TableRow>
							<Cell>Price</Cell>
							<Cell>Change</Cell>
							<Cell>Perf1M</Cell>
							<Cell>Perf3M</Cell>
							<Cell>Perf1Y</Cell>
							<Cell>Company</Cell>
							<Cell>Industry</Cell>
						</v.TableRow>
					</v.TableHead>
					<v.TableBody>
					{
						data.map(item=>(
							<v.TableRow key={item.Sym}>
								<Cell>{item.Price}</Cell>
								<Cell>{item.Change}</Cell>
								<Cell>{item.Perf1M}</Cell>
								<Cell>{item.Perf3M}</Cell>
								<Cell>{item.Perf1Y}</Cell>
								<Cell>{item.Company}</Cell>
								<Cell>{item.Industry}</Cell>
							</v.TableRow>
						))
					}
					</v.TableBody>
				</v.Table>
			</v.Scrollbars>
			</div>
		</v.CardContent>
	</div>
)))


