import React from 'react'

import * as P from "./ComponentPart"
import * as R from "./ComponentRender"
import * as v from "./vendor"
import * as u from "./utils"

// import { uiStore } from "./service_store_ui"

const _numPerPage = Math.floor((window.innerHeight - 32 - 2*56)/32)-1

const Row = v.withStyles(
	theme => ({
		root: {
			height:Math.floor((window.innerHeight - 32 - 2*56) / (_numPerPage+1)),
			whiteSpace: "nowrap"
		},
	})
)(
	({ classes, children }) => <v.TableRow className={classes.root}>{children}</v.TableRow>
)

const Cell = v.withStyles(
	theme => ({
		root: {
			padding: theme.spacing.unit * 0.5,
		},
		rootClickable: {
			padding: theme.spacing.unit * 0.5,
			cursor:"pointer",
		},
	})
)(
	({ classes, children, onClick, color, numeric }) => 
		<v.TableCell numeric={!!numeric} onClick={onClick} style={{color}} className={onClick ? classes.rootClickable : classes.root}>{children}</v.TableCell>
)

const CellNumber = v.withStyles(
	theme => ({
		rootGreen: {
			padding: theme.spacing.unit * 0.5,
			color:"green"
		},
		rootRed: {
			padding: theme.spacing.unit * 0.5,
			color:"red"
		},
	})
)(
	({ classes, value }) => (
		<v.TableCell numeric className={value > 0 ? classes.rootGreen : classes.rootRed}>
			{value && (value*100).toFixed(1) + "%"}
		</v.TableCell>
	)
)

const sortStocks = (sortBy, desc) => (a, b) => desc ? b[sortBy] - a[sortBy] : a[sortBy] - b[sortBy]

const RenderScreener = v.inject("ui")(v.observer(
	class extends React.Component {
		state = {page: 1, sortBy: this.props.sortBy, desc:true, currentStockIndex:-1}

		maxPage = Math.floor((this.props.data.length / _numPerPage))+1

		prevPage = () => {
			if (this.state.page > 1) {
				this.setState({page: this.state.page - 1})
			}
		}
		nextPage = () => {
			if (this.state.page < this.maxPage) {
				this.setState({page: this.state.page + 1})
			}
		}

		handlerSort = (sortBy) => () => {
			if (this.state.sortBy === sortBy) {
				const desc = !this.state.desc
				this.setState({page:1, desc})
				this.props.data.sort(sortStocks(sortBy, desc))
			} else {
				this.setState({page:1, sortBy, desc:true})
				this.props.data.sort(sortStocks(sortBy, true))
			}
		}

		getColor = (current) => {
			if (this.state.sortBy === current) {
				if (this.state.desc) {
					return "green"
				}
				return "red"
			}
			return null
		}

		// openStock = symbol => () => {
		// 	this.props.ui.OpenSide("/finance/stock", symbol)
		// }

		openStockByIndex = i => () => {
			this.setState({currentStockIndex: i})
			this.props.ui.OpenSide("/finance/stock", this.props.data[i].Symbol)
		}

		onArrow = e => {
			if (e.code === "ArrowUp") {
				if (this.state.currentStockIndex > 0) {
					this.openStockByIndex(this.state.currentStockIndex-1)()
				}
			} else if (e.code === "ArrowDown") {
				if (this.state.currentStockIndex < this.props.data.length-1 ) {
					this.openStockByIndex(this.state.currentStockIndex+1)()
				}
			}			
		}

		componentDidMount() {
			if (this.props.sortBy.indexOf("ChangePercent") === -1) {
				this.setState({sortBy:"Change"})
				this.props.data.sort(sortStocks("Change", this.state.desc))
			} else {
				this.props.data.sort(sortStocks(this.props.sortBy, this.state.desc))
			}

			window.addEventListener("keydown", this.onArrow)
		}

		componentWillUnmount() {
			window.removeEventListener("keydown", this.onArrow)
		}

		render() {
			const { page } = this.state
			const data = this.props.data.slice((page-1)*_numPerPage, page*_numPerPage)

			return (
				<React.Fragment>
					<P.NavBar color="default">
						<P.NavBarTitle label={`${page} / ${this.maxPage}`} />
						<P.NavBarIcon Icon={v.ChevronLeftIcon}  onClick={this.prevPage} />
						<P.NavBarIcon Icon={v.ChevronRightIcon} onClick={this.nextPage} />
					</P.NavBar>

					<div style={{padding:"8px 16px", height:window._.fullHeight - 56*2 -32}}>
						<div style={{width:60, float:"left"}}>
							<v.Table>
								<v.TableHead><Row><Cell>Symbol</Cell></Row></v.TableHead>
								<v.TableBody>
							{
								data.map((stock, idx)=>(
									<Row key={stock.Symbol}>
										<Cell onClick={this.openStockByIndex(idx+(page-1)*_numPerPage)}>{stock.Symbol}</Cell>
									</Row>
								))
							}
								</v.TableBody>
							</v.Table>
						</div>
						<div style={{width:"calc(100% - 60px)", float:"left"}}>
						<v.Scrollbars autoHeight autoHeightMin={window._.fullHeight - window._.toolbarHeight*2-32} autoHeightMax={window._.fullHeight - window._.toolbarHeight*2-32}>
							<v.Table>
								<v.TableHead>
									<Row>
										<Cell numeric>Price</Cell>
										<Cell numeric onClick={this.handlerSort("Change")}   color={this.getColor("Change")}>Change</Cell>
										<Cell numeric onClick={this.handlerSort("Day5ChangePercent")}   color={this.getColor("Day5ChangePercent")}>Perf5D</Cell>
										<Cell numeric onClick={this.handlerSort("Month1ChangePercent")} color={this.getColor("Month1ChangePercent")}>Perf1M</Cell>
										<Cell numeric onClick={this.handlerSort("Month3ChangePercent")} color={this.getColor("Month3ChangePercent")}>Perf3M</Cell>
										<Cell numeric onClick={this.handlerSort("Year1ChangePercent")}  color={this.getColor("Year1ChangePercent")}>Perf1Y</Cell>
										<Cell>Industry</Cell>
									</Row>
								</v.TableHead>
								<v.TableBody>
								{
									data.map(Stock=>(
										<Row key={Stock.Symbol}>
											<Cell numeric>{Stock.LatestPrice && Stock.LatestPrice.toFixed(2)}</Cell>
											<CellNumber value={Stock.Change} />
											<CellNumber value={Stock.Day5ChangePercent} />
											<CellNumber value={Stock.Month1ChangePercent} />
											<CellNumber value={Stock.Month3ChangePercent} />
											<CellNumber value={Stock.Year1ChangePercent} />
											<Cell>{u.CidName(Stock.Cid)}</Cell>
										</Row>
									))
								}
								</v.TableBody>
							</v.Table>
						</v.Scrollbars>
						</div>
					</div>
				</React.Fragment>
			)
		}
	}
))

export const PageFinanceScreener = ({path, state}) => (
	<R.LoadContent path={path} state={state}>
		{({Screener}) => <RenderScreener data={Screener} sortBy={state} />}
	</R.LoadContent>
)



