import React from 'react'

import * as P from "./ComponentPart"
import * as R from "./ComponentRender"
import * as v from "./vendor"
import * as u from "./utils"

import { fetch_data } 	from "./service_api"

class LikeButton extends React.PureComponent {
	state = {isLiked: !!this.props.isLiked, isLoading: false}

	toggle = () => {
		if (this.state.isLoading) {
			return
		}
		this.setState({isLoading: true})
		const action = this.state.isLiked ? "unlike" : "like"
		fetch_data("/user/settings", {symbol:this.props.symbol, action}).then(c => {
			if (c) {
				this.setState({isLiked: !this.state.isLiked, isLoading: false})
			}
		})
	}

	render() {
		if (this.state.isLiked) {
			return <v.IconButton color="secondary" onClick={this.toggle}><v.FavoriteIcon /></v.IconButton>
		}
		return <v.IconButton onClick={this.toggle}><v.FavoriteBorderIcon /></v.IconButton>
	}
}

export const PageFinanceStock = ({path, state}) => (
	<R.LoadContent path={path} state={state}>
		{({Stock}) => {
			if (!Stock) {
				return <P.CenterInfo label="This symbol is not found" />
			}

			const imgHeight = Math.ceil(340 * window._.fullWidth / 700)

			return (
				<React.Fragment>
					<v.CardHeader
						title={Stock.Company}
						subheader={[u.CidName(Stock.Cid), Stock.Country, Stock.EarningsDate || "N/A"].join(" | ")}
						action={<LikeButton symbol={Stock.Symbol} isLiked={Stock.IsLiked} />}
					/>
					<v.CardContent>
						<img width="100%" style={{border:"1px gray solid"}} height={imgHeight} alt="chart" src={`https://www.finviz.com/chart.ashx?t=${Stock.Symbol}&ty=c&ta=1&p=d&s=l`} />
					</v.CardContent>
					<v.CardContent>
						<v.Typography component="h3" variant="title">
							Description
						</v.Typography>
						<v.Typography component="p">{Stock.Description}</v.Typography>
						<v.Typography component="p">
							<a style={{paddingRight:8}} target="_blank" href={"https://www.sec.gov/cgi-bin/browse-edgar?action=getcompany&CIK="+Stock.Symbol+"&type=10-&dateb=&owner=exclude&count=10"}>10Q/10K</a>
							<a style={{paddingRight:8}} target="_blank" href={"https://finance.yahoo.com/quote/"+Stock.Symbol}>Yahoo</a>
							<a style={{paddingRight:8}} target="_blank" href={"https://www.finviz.com/quote.ashx?t="+Stock.Symbol}>Finviz</a>
							<a style={{paddingRight:8}} target="_blank" href={"https://stocktwits.com/symbol/"+Stock.Symbol}>StockTwits</a>
							{ Stock.Website && <a target="_blank" href={Stock.Website}>website</a> }
						</v.Typography>
					</v.CardContent>
					{
						Stock.EventFDA &&
						<v.CardContent>
							<v.Typography component="h3" variant="title">
								Upcoming FDA Deadline
							</v.Typography>
							{Stock.EventFDA.map((item, idx)=>(
								<v.Typography component="p" key={idx}>
									{item.Deadline}({item.Stage}): {
										item.Href ? <a target="_blank" href={item.Href}>{item.Drug}</a> : item.Drug
									}
								</v.Typography>
							))}
						</v.CardContent>
					}
					{
						Stock.EventConference &&
						<v.CardContent>
							<v.Typography component="h3" variant="title">
								Upcoming Conference
							</v.Typography>
							{Stock.EventConference.map((item, idx)=>(
								<v.Typography component="p" key={idx}>
									{item.Start} - {item.End}: {
										item.EventHref ? <a target="_blank" href={item.EventHref}>{item.EventTitle}</a> : item.EventTitle
									}
								</v.Typography>
							))}					
						</v.CardContent>
					}
				</React.Fragment>
			)
		}}
	</R.LoadContent>
)



