import React from 'react'

import * as v from "./vendor"
import * as u from "./utils"

const make = m => Object.keys(m).map(slug=>({slug, label:m[slug]}))

const screenerMaps = {
	"Screener": 	make(u.ScreenerMap),
	
	"Performance": 	make(u.PerformanceMap),

	"Basic Materials":    		make(u.IndustryBasicMaterialsMap),
	"Communication Services":   make(u.IndustryCommunicationServicesMap),
	"Consumer Cyclical":    	make(u.IndustryConsumerCyclicalMap),
	"Consumer Defensive":    	make(u.IndustryConsumerDefensiveMap),
	"Energy":   		 		make(u.IndustryEnergyMap),
	"Financial":   				make(u.IndustryFinancialMap),
	"Healthcare":    			make(u.IndustryHealthcareMap),
	"Industrials":    			make(u.IndustryIndustrialsMap),
	"Real Estate":    			make(u.IndustryRealEstateMap),
	"Technology":    			make(u.IndustryTechnologyMap),
	"Utilities":    			make(u.IndustryUtilitiesMap),
}

const MenuGroup = v.inject("ui")(v.observer(
	class extends React.Component {
		state ={ open: false }

		handleOpen = () => {
			this.setState({ open: !this.state.open })
		}

		openScreener = slug => () => this.props.ui.OpenMinor("/finance/screener", slug)

		render() {
			const { label, items } = this.props

			return (
				<React.Fragment>
					<v.CardHeader
						title={label}
					/>
					<v.CardContent>
						{
							items.map(item=>(
								<v.Button key={item.slug} color="primary" onClick={this.openScreener(item.slug)}>{item.label}</v.Button>
							))
						}
					</v.CardContent>
				</React.Fragment>
			)
		}		
	}
))

export const PageFinance = v.inject("ui")(v.observer(({ui})=>{
	return (
		<React.Fragment>
		{
			Object.keys(screenerMaps).map(label => <MenuGroup key={label} label={label} items={screenerMaps[label]} />)
		}
		</React.Fragment>	
	)
}))



