import { uiStore } from "./service_store_ui"
import * as u from "./utils"

const apiPrefix = function(){
	if (window.location.port === "3000") {
		return "http://apihtyapp.lizh.me"
	}
	return "https://api.lizh.me"
}()

// uiStore.LoadingOn()
// uiStore.LoadingOff()
// uiStore.PutMessage(msg)
// uiStore.SetAuth(false)

// https://www.cnblogs.com/wonyun/p/fetch_polyfill_timeout_jsonp_cookie_progress.html
function start_fetch(url, obj) {
	obj.token = u.Storage.User().AuthToken

	uiStore.LoadingOn()
	var fetchPromise = fetch(apiPrefix + url, {
		method:"POST",
		body: JSON.stringify(obj),
		headers: { "Content-Type": "application/json", 'AuthToken': localStorage["AuthToken"], 'Accept': "application/json" },
	})

	var timeoutPromise = new Promise(function(resolve, reject){
		setTimeout(() => reject(new Error("超时")), 10000)
	})

	return Promise.race([fetchPromise, timeoutPromise])
}

function PromiseNull(msg) {
	uiStore.PutMessage(msg)
	uiStore.LoadingOff()
	return new Promise((resolve, reject)=>{
		resolve(null)
	})
}

function PromiseData(data, msg) {
	uiStore.PutMessage(msg)
	uiStore.LoadingOff()
	return new Promise((resolve, reject)=>{
		resolve(data)
	})
}

export async function fetch_data(url, obj) {
	if (!window.navigator.onLine) {
		return PromiseNull("离线状态")
	}

	let res
	try {
		res = await start_fetch(url, obj)
	} catch(e) {
		return PromiseNull(`网络错误`)
	}

	if (res.status >= 200 && res.status < 300) {
		const data = await res.json()
		if (data.Payloads && data.Payloads.SyncError) {
			uiStore.SetAuth(false)
			localStorage.clear()
		}
		if (data.Status !== 0 ) {
			return PromiseNull(data.Message)
		} else {
			return PromiseData(data.Payloads, data.Message)
		}
	} else if (res.status === 401) {
		return PromiseNull("无权限，请重新登录")
	} else if (res.status === 429) {
		return PromiseNull("请求频率过高")
	} else if (res.status >= 500) {
		return PromiseNull("服务器内部错误")
	} else {
		return PromiseNull(`Error Code: ${res.status}`)
	}
}



