import * as v from "./vendor"
import * as u from "./utils"

import { uiStore } 		from "./service_store_ui"
import { fetch_data } 	from "./service_api"

const UserModel = v.types.model("UserModel", {
	AuthToken:   "",

	LastUpdated: 0,
	Stockparams: v.types.optional(v.types.array(v.types.number), [2.0, 60.0, 300.0, 20000.0, 0.3, 2.0]),
	Timezone:    "ny",
	ColorStyle:  "green",
	Portfolio:   v.types.optional(v.types.map(v.types.array(v.types.string)),{"default": []}),
	WatchList:   v.types.optional(v.types.array(v.types.string),[]),
})
.views(self => ({
	IsLikeThisSymbol(symbol) {
		if (self.WatchList.indexOf(symbol) >= 0) {
			return true
		}
		return false
	}
}))
.actions(self => {
	v.addMiddleware(self, u.mst_mw_atomic)

	function _clearLogin() {
		uiStore.SetAuth(false)
		localStorage.clear()
		// setTimeout(()=>localStorage.clear(), 4000)
	}

	function _sync(init = false) {
		let LastUpdated = self.LastUpdated
		if (init) {
			LastUpdated = 0
		}

		return fetch_data("/user/sync", {
			token: self.AuthToken,
			LastUpdated,
		})
	}

	function _after(payloads){
		if (payloads) {
			uiStore.SetAuth(true)
			if (payloads.User) {
				v.applySnapshot(self, payloads.User)
			}
			if (payloads.AuthToken) {
				self.AuthToken = payloads.AuthToken
			}
			return new Promise((resolve, reject)=>{ resolve(true) })
		}
		return new Promise((resolve, reject)=>{ resolve(false) })
	}

	return {
		afterCreate: v.flow(function*(){
			v.applySnapshot(self, u.Storage.User())
			if (self.LastUpdated > 0) {
				_after(yield _sync())
			} else {
				_clearLogin()
			}
		}),

		Register: v.flow(function*(email, password1, password2, language){
			localStorage.clear()
			return _after(yield fetch_data("/user/register", {email, password1, password2, language}))
		}),
		Login: v.flow(function*(email, password){
			localStorage.clear()
			return _after(yield fetch_data("/user/login", {email, password}))
		}),

		LikeSymbol: (sym) => {
			if (self.WatchList.indexOf(sym) === -1) {
				self.WatchList.push(sym)
			}
		},

		UnlikeSymbol: (sym) => {
			const arr = self.WatchList.slice()
			const pos = arr.indexOf(sym)
			self.WatchList = arr.slice(0, pos).concat(arr.slice(pos+1))
		},

	}
})

export const userStore = UserModel.create()

v.onSnapshot(userStore, snapshot=>{
	u.Storage.PutUser(snapshot)
})



